@charset "utf-8";

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

body {
    color: #333;
}

.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
}

.check form {
    position: relative;
}

li .item {
    position: relative;
}



.check form input[type="text"] {
    width: 100%;
    height: 50px;
    border: 1px solid #999;
}



button[type="button"] { /* 버튼 전체 */
    border: 0;
    background-color: transparent;
}

.check form button {
    /* 추가하기 버튼 */
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
}

li .item button {
    position: absolute;
}





.check form button img {
}

.check .check_list {
}




.check .check_list li {
    margin: 30px 0; /* 각 아이템 별 간격이 생기네 */
    border: 1px solid#3d3d3d;
    border-radius: 5px;
    background-color: #fffeae;
    width: 100%;
    height: 80px;
    line-height: 80px;
    font-size: 20px;
}




